<template>
  <div class="connectwifi">
       <head-top title="Connecting To Router" go-back="true"></head-top>
       <div class="circle_progress">
         <div class="progress_bar">
          <svg xmlns="http://www.w3.org/200/svg" height="160px" width="160px">
            <circle class="circle" cx="80px" cy="80px" :r="radius + 'px'" fill="none" stroke="#CFDFE2" stroke-width="5px" stroke-dasharray="586,10000" stroke-linecap="round" />
          </svg>
          <svg xmlns="http://www.w3.org/200/svg" height="160px" width="160px" class="circle_top">
            <circle class="circle" cx="80px" cy="80px" :r="radius + 'px'" fill="none" stroke="#01a29c" stroke-width="5px" :stroke-dasharray="strokeDasharray" :stroke-dashoffset="strokeDashoffset" stroke-linecap="round" />
          </svg>
          <div class="icon" :style="{transform: 'rotate('+ percentage/100*360 +'deg)'}">
            <img src="../../../assets/addevice/click@3x.png">
          </div>
          <div class="number">
            <span>{{percentage}}%</span>
          </div>
         </div>
       </div>
       <div class="prompt">
           <p>Device is connecting to router</p>
           <p>Try to make your routers, mobile phones and devices close to each other</p>
           <p ref="eltime">{{timeText}}</p>
       </div>
       <!-- 退出配网 -->
       <messageBox setHeight="170" setType="confirm" v-show="messageBoxType==='exitConnect'">
        <div slot="confirmColumn" class="common_alert unconnectalert">
          <div class="alert_describe" style="text-align: left">Are you sure to quit the current distribution network?</div>
          <div class="alert_operation">
            <div class="determine_two" @click="alertChoose(0)" style="background:#cccccc;">Cancel</div>
            <div class="determine_two" @click="alertChoose(1)" style="background:#13af68">ok</div>
          </div>
        </div>
      </messageBox>
  </div>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import { stopConnectionNetworkRequest } from '../../../api/method.js'
import { connectionNetworkResponse, switchNetworkResponse } from '../../../api/getData.js'
import headTop from '../../../components/common/head'
import messageBox from '../../../components/common/messageBox'
export default {
  data () {
    return {
      time: '02:00',
      timeText: `2 minutes 0 .seconds`,
      radius: 75, // svg进度条半径
      percentage: 0, // 百分比
      timer: null, // 定时器
      barTimer: null // 定时器
    }
  },
  components: {
    headTop,
    messageBox
  },

  computed: {
    ...mapGetters(['messageType', 'currentRouter']),
    messageBoxType () {
      return this.messageType
    },
    strokeDasharray () {
      return (Math.floor(2 * Math.PI * this.radius) + ',' + Math.floor(2 * Math.PI * this.radius))
    },

    strokeDashoffset () {
      return Math.floor(2 * Math.PI * this.radius) - Math.floor(2 * Math.PI * this.radius) / 100 * this.percentage
    }
  },
  mounted () {
    // 设备连接路由器网络返回
    console.log(this.currentRouter)
    connectionNetworkResponse(this.NAVIGATOR, this)
    switchNetworkResponse(this.NAVIGATOR, this, 'connectRouter')
    if (this.$root.ISDEBUG) {
      setTimeout(() => {
        window.connectionNetworkResponse(true)
        window.switchNetworkResponse('MOBILE', 1)
      }, 30000)
    }
    // setTimeout(() => {
    //   this.$router.push({path: '/Me/choose'})
    // }, 3000)
    this.timedown()
    this.barTimedown()
  },
  destroyed () {
    clearInterval(this.timer) // 清除定时器
    clearInterval(this.barTimer) // 清除定时器
  },
  methods: {
    ...mapActions(['showMessagesBox']),
    timedown () {
      let m = parseInt(this.time.split(':')[0])
      let s = parseInt(this.time.split(':')[1])
      this.timer = setInterval(() => {
        if (--s < 0) {
          s = 59
          m--
        }
        // this.time = (m < 10 ? `0${m}` : m) + ':' + (s < 10 ? `0${s}` : s)
        this.timeText = `${m} ${'minutes'} ${s} ${'seconds'}`
        if (m === 0 && s === 0) {
          clearInterval(this.timer)
          this.$router.push({path: '/Me/configurefailed'})
          // 停止配置网络
          stopConnectionNetworkRequest(this.NAVIGATOR, this)
        }
      }, 1000)
    },

    barTimedown () {
      this.barTimer = setInterval(() => {
        if (++this.percentage === 100) {
          clearInterval(this.barTimer) // 清除定时器
        }
      }, 1000 * 180 / 100)
    },

    alertChoose (num) {
      switch (num) {
        case 0:
          this.showMessagesBox({type: null})
          break
        case 1:
          // 停止配置网络
          stopConnectionNetworkRequest(this.NAVIGATOR, this)
          this.showMessagesBox({type: null})
          this.$router.push({path: '/Me/configwifi'})
          break
        default:
          break
      }
    }
  }
}
</script>
<style lang="less" scoped>
 @import '../../../styles/mixin.less';
  .connectwifi{
   .fs;
   background: #ffffff;
   .bluetooth{
      .wh(1.066667rem, 100%);
      margin-left: .2rem;
      img{
        height: .533333rem;
        vertical-align: middle;
      }
    }
    .circle_progress{
      .wh(100%, 450px);
      padding-top: 80px;
      .progress_bar{
        .wh(320px, 320px);
        margin: 0 auto;
        position: relative;
        .circle_top{
          position: absolute;
          top: 0;
          left: 0;
          transform-origin: center;
          transform: rotate(-90deg);
          .circle{
            transition: all 1.8s ease-in-out;
          }
        }
        .icon, .number{
          width: 152px; /* no */
          height: 152px; /* no */
          border-radius: 50%;
          position: absolute;
          top: 4px; /* no */
          left: 4px; /* no */
          transform-origin: center;
          transition: all 1.8s ease-in;
          // border: 1px solid red;
          img{
            height: 20px; /* no */
            position: absolute;
            top: -8px; /* no */
            left: 64px; /* no */
          }
        }
        .number{
          .wh-l(180px,180px);
          top:50%;
          margin-top: -90px;
          left: 50%;
          margin-left: -90px;
          span{
            display: block;
            margin: 0 auto;
            color: @themeColor;
            font-size: 46px;
            font-weight: 700;
          }
        }
      }
    }
   .prompt{
        width:100%;
        text-align: center;
        font-size: 28px;
        p:nth-child(1){
            color: #585858;
            margin-top: 16px;
            font-size: 34px;
        }
        p:nth-child(2){
            color: #b7b7b7;
            margin: 0 auto;
            margin-top:28px;
            line-height: 71px;
            width:520px;
        }
         p:nth-child(3){
            color: #585858;
            margin-top:28px;
            line-height: 71px;
            font-size: 28px;
        }
    }
    .unconnectalert .alert_describe{
      padding:  0 50px;
      // word-break:break-all;
    }
  }
</style>



